@use 'base/variables' as v;
@use 'base/mixins' as m;

.dashboard {
    background-color: v.$grisClaro;
     min-height: 100vh;

    @include m.tablet {
        display: flex;
       
    }

    .principal {
        flex: 1;
        // overflow: scroll;
    }

    .contenido {
        padding: 2rem;
        .nombre-pagina {
            font-size: 2.8rem;
            text-align: center;
        }
    }

    .formulario {
        input:not([type="submit"]){
            background-color: v.$blanco;
        }

        input[type="submit"] {
            @include m.boton (v.$indigo, v.$blanco);
            padding: 1rem;
            text-transform: uppercase;
            font-size: 1.4rem;
        }
    }
}