@use "base/mixins" as m;
@use "base/variables" as v;

.formulario {
  margin-top: 5rem;
  .campo {
    display: flex;
    align-items: center;
    margin-bottom: 2rem;
    label {
        flex: 0 0 10rem;
    }
    input {
        flex: 1;
        padding: 1rem;
        line-height: 0.5;
        border: none;
        background-color: lighten(v.$gris, 40%);
    }
  }
}
